.about{
    &-content{
        display: flex;
        gap:20px;
        padding: 20px;
        width: 100%;
        
    }
    
}
.personalwrapper ,.pwrapper{
    width: 50%;
}
.personalwrapper{
    h3{
        margin-bottom: 10px;
        font-size: 2.5rem;
        font-weight: 700;
        color: var(--yellow-theme-main-color);
    }
    h3.piheadertext{
        padding-top: 20px;
    }
    p{
        color:var(--yellow-theme-sub-text-color);
        font-size: 1.8rem;
        line-height: 1.65;
        padding-bottom: 20px;
        border-bottom: 1px dashed var( --yellow-theme-main-color);
    }
    ul>li{
        line-height: 1rem;
        margin-bottom: 15px;

        span.title, span.value{
            font-size: 18px;
            font-weight: 700;
            letter-spacing: 0.5px;
            border-bottom: 2px solid var( --yellow-theme-main-color);
            position: relative;
            display: inline-block;
            margin-right: 16px;
            padding-bottom: 5px;
            color: var(--yellow-theme-sub-text-color);
            border: none;
        }
        
    }

}
.profile-picture{
    display:flex;
    align-items:center;
    justify-content: center;
    box-shadow: 0 1px 0px 0px var(--white);
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 0 0px 0 100px;
}
.profile-picture-background{
    height: 92%;
    width: 92%;
    background-image:url(profilephoto.jpg) ;
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}
@media only screen and (max-width: 760px){
.about-content{
    flex-direction: column;
    .personalwrapper,.pwrapper{
        width:100px;
    }
}
.profile-picture{
    display:flex;
    align-items:center;
    justify-content: center;
    box-shadow: 0 1px 0px 0px var(--white);
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 0 0px 0 100px;
}
.profile-picture-background{
    height: 85%;
    width: 80%;
    background-image:url(profilephoto.jpg) ;
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}
}