.skills{
    &-content-wrapper{
        display: flex;
        padding: 20px;
        gap: 20px;
    }

    .wrapper-ic{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .text{
        display: inline-block;
        color: var(--yellow-theme-main-color);
        font-size: 22px;
        font-weight: 700;
        line-height: 1rem;
        letter-spacing: 0.5px;
        position: relative;
        text-transform: uppercase;
        padding: 5px 10px;
        border-bottom: 2px solid var(--yellow-theme-main-color);
    }
    .text::before,.text::after{
        position: absolute;
        content: '';
        width: 2px;
        height: 5px;
        background-color: var(--yellow-theme-main-color);
        bottom: -1px;
    }
    .text::before{
        left: 0;
    }
    .text ::after{
        right: 0;
    }
    .progressbar{
        width: 100%;
    }
    .bar{
        padding: 50px 0px 0px 0px ;

        p{
            font-size: 1.6rem;
            margin: 0 0 10px;
            font-weight:500 ;
            text-transform: uppercase;
            color: var(--yellow-theme-sub-text-color);

        };
    }
}
@media only screen and (max-width: 768px) {
    .skills{
        &-content-wrapper{
            flex-direction: column;
        }
    }

        .progressbar{
             margin-bottom: 20px;
        }
    
}